import { Component, OnInit, SimpleChanges, Input, EventEmitter, Output } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { BlockCostTakittaModalComponent } from './block-cost-takitta-modal/block-cost-takitta-modal.component';
import { FormBuilder, FormGroup, FormControl } from '@angular/forms';
import { CampaignService } from '../../_services/Campaign.service';
import { ConfirmModalComponent } from '../../common-modal/confirm-modal/confirm-modal.component';
import { AlertModalComponent } from '../../common-modal/alert-modal/alert-modal.component';

@Component({
  selector: 'app-block-cost-takitta',
  templateUrl: './block-cost-takitta.component.html',
  styleUrls: ['./block-cost-takitta.component.css']
})
export class BlockCostTakittaComponent implements OnInit {
  @Output() childEvent = new EventEmitter();
  @Input() blockTakittaMonthList: any[];
  @Input() formZone: FormGroup;
 

  constructor(public _matDialog: MatDialog,
    private confirmDialog: MatDialog,
    private formBuilder: FormBuilder,
    private alertDialog: MatDialog,
    private campaignService: CampaignService) { }


  ngOnInit(): void {
  }


  ngOnChanges(changes: SimpleChanges) {
    this.formZone = new FormGroup({});
    this.loadCost();

  }

  loadCost() {
    if (this.blockTakittaMonthList.length > 0) {
      for (var i = 0; i < this.blockTakittaMonthList.length; i++) {
        // let costObj: any;

        var costObj = this.blockTakittaMonthList[i];
        //   alert(costObj['ActualAb'])
        this.formZone.addControl("fromActualAb_" + i, new FormControl(costObj['ActualAb']));
        this.formZone.updateValueAndValidity();
        costObj['fromActualAb'] = "fromActualAb_" + i;

      }
    }
  }

  summary() {
    var result = 0;
    this.blockTakittaMonthList.forEach(function (data) {
      result += data["BudgetCf"];
    })
    return result;
  }

  summaryActual() {
    var result = 0;
    const self = this;
    this.blockTakittaMonthList.forEach(function (data) {
      if (self.formZone.get(data["fromActualAb"]).value != null) {
        result += parseFloat(self.formZone.get(data["fromActualAb"]).value);
      } else {
        result += 0;
      }
    });

    return result;
  }


  save() {

    const self = this;

    this.blockTakittaMonthList.forEach(function (data) {

      data["ActualAb"] = parseFloat(self.formZone.get(data["fromActualAb"]).value);
    });


    const dialogRef = this.confirmDialog.open(ConfirmModalComponent, {
      disableClose: true,
      data: {
        message: 'Would you like to save?',
        buttonText: {
          ok: 'Save',
          cancel: 'No'
        }
      }
    });

    dialogRef.afterClosed().subscribe((confirmed: boolean) => {
      if (confirmed) {

        this.campaignService.updateBlockTakittaActual(this.blockTakittaMonthList).subscribe(
          result => {
            const alertDialog = this.alertDialog.open(AlertModalComponent, {
              disableClose: true,
              data: {
                message: 'Save Successfully.',
                buttonText: {
                  cancel: 'OK'
                }
              }
            });
            alertDialog.afterClosed()
              .subscribe(response => {
                this.childEvent.emit('takitta Actual update');
                //  this.matDialogRef.close('complete')
              });
          },
          error => console.error(error)
        )
      }
    });

  }

  openUpdateCost(id) {
    this.campaignService.viewBlockTakittaMonth(id).subscribe(
      result => {
        
    const alertDialog = this._matDialog.open(BlockCostTakittaModalComponent, {
      //panelClass: 'select-reviewer-modal',
      disableClose: true,
      maxWidth: undefined,
      height: '1200Px',
      width: '1000px',
      data: {
        resolverData: result.resolverData
      }
    });

    alertDialog.afterClosed()
      .subscribe(response => {
        if (response != 'Close') {
          //   self.isSearch();
          //   self.cdr.detectChanges();
          this.childEvent.emit('takitta update');
        }
      });
      }
      , error => console.error(error));


  }
}
