import { ChangeDetectorRef, Component, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { DataTableDirective } from 'angular-datatables';
import { CommonUtilService } from '../../_helpers/CommonUtilService';
import { MasterDataService } from '../../_services/MasterData.service';
declare var pagingInputInit: any;
import { thDatatables } from '../../_helpers/thDatatables';

@Component({
  selector: 'app-extra-budget-management',
  templateUrl: './extra-budget-management.component.html',
  styleUrls: ['./extra-budget-management.component.css']
})
export class ExtraBudgetManagementComponent implements OnInit {

  constructor(private MasterData: MasterDataService,
    public _matDialog: MatDialog,
    private cdr: ChangeDetectorRef,
    private router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,) { }

  @Input() formZone: FormGroup;
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any;
  dialogRef: any;

  activeList: any[] = [{
    Code: "Y",
    Description: "Active"
  }, {
    Code: "N",
    Description: "Inactive"
  }];

  public dataa = [
    { seq: '1', name: 'Freight and Customs', type: 'ZPQ', activity: 'Production', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
    { seq: '2', name: 'Domestic', type: 'Rig', activity: 'M9 East Appraisal', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
    { seq: '3', name: 'MGTC', type: 'Rig', activity: 'M9 East Appraisal', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' }
  ];

  ngOnInit(): void {

    this.formZone = this.formBuilder.group({
      'ExtraBudget': new FormControl({ value: '', disabled: false }),
      'ActiveFlag': new FormControl({ value: true, disabled: false })
    });

    pagingInputInit();
    this.dtOptions = {
      pagingType: 'input',
      dom: 'B<"float-right"l>tip',
      language: thDatatables,
      processing: true,
      serverSide: true,
      responsive: true,
      scrollX: true,
      autoWidth: true,
      pageLength: 10,
      lengthMenu: [10, 25, 50, 100],
      //Configure the buttons
      buttons: [
        {
          text: '+ New',
          className: 'mat-focus-indicator btn-green mat-raised-button mat-button-base _mat-animation-noopable button-margin-bottom',
          action: () => {
            this.isAdd();
          },
          init: function (api, node, config) {
            $(node).removeClass('dt-button')
          }
        }
      ],
      "columns": [
        {
          data: null,
          className: 'dt-head-center dt-body-center',
          width: '10px',
          render: this.renderAction
        },
        {
          data: 'seqnum',
          className: 'dt-head-center dt-body-center',
          "width": "10px"
        },
        {
          data: 'Extra_Budget',
          className: 'dt-head-center dt-body-center',
          "width": "50px"
        },
        {
          data: 'Active_Flag',
          //sortProperty: 'countReservoir',
          className: 'dt-head-center dt-body-center',
          width: '50px',
          render: this.activeFlagData
        },
        {
          data: 'Update_Dttm',
          //sortProperty: 'ApproveName',
          className: 'dt-head-center dt-body-center',
          width: '100px',
          render: CommonUtilService.formatDateTime
        },
        {
          data: 'Update_Name',
          //sortProperty: 'ApproveDttm',
          className: 'dt-head-center dt-body-center',
          width: '100px'
        }
      ],
      columnDefs: [
        {
          "targets": [0, 1],
          "orderable": false
        }
      ],
      order: [[2, 'asc']],
      ajax: (dataTablesParameters: any, callback) => {
        var criteria = {
          'start': dataTablesParameters.start,
          'length': dataTablesParameters.length,
          "sorterProperty": (this.dtOptions.columns[dataTablesParameters.order[0].column].sortProperty) ?
            this.dtOptions.columns[dataTablesParameters.order[0].column].sortProperty :
            this.dtOptions.columns[dataTablesParameters.order[0].column].data,
          "direction": dataTablesParameters.order[0].dir,
          "ExtraBudget": this.formZone.get("ExtraBudget").value,
          "ActiveFlag": (this.formZone.get("ActiveFlag").value == "Y") ? true : (this.formZone.get("ActiveFlag").value == "N") ? false : null,
        }

        this.MasterData.searchExtraBudgetPaging(criteria).subscribe(res => {
          callback({
            recordsTotal: res.iTotalRecords,
            iTotalDisplayRecords: res.iTotalDisplayRecords,
            recordsFiltered: res.recordsFiltered,
            data: res.detail
          });
        });
      },
      createdRow: (row: any, data: any | Object, index: number) => {
        const self = this;

        row.querySelectorAll('.mat-edit').forEach(function (item) {
          item.addEventListener('click', function ($event) {
            self.router.navigate(['/extra-budget-management-modal', data.IdEncoded, "edit"]);
          });
        });
      }
    }
  }

  activeFlagData(data) {
    if (data == true) {
      return "Active";
    }
    return "Inactive";
  }

  renderAction(data, type, full, meta) {
    //console.log(data);
    return '<mat-icon role="img"  matripple="" class="gray-icon mat-icon notranslate mat-ripple material-icons mat-edit" style="cursor: pointer;" aria-hidden="true" ng-reflect-color="primary" ng-reflect-radius="15" title="Edit">edit</mat-icon>'
  }

  isSearch() {
    //this.isFirst = false;
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.ajax.reload();
    });
  }

  isAdd() {
    this.router.navigate(['/extra-budget-management-modal', 0, "add"]);
  }

  isClear() {
    this.formZone.get("ExtraBudget").setValue(null);
    this.formZone.get("ActiveFlag").setValue(null);
    this.isSearch();
  }
}
