import { ChangeDetectorRef, Component, Input, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { ActivatedRoute, Router } from '@angular/router';
import { DataTableDirective } from 'angular-datatables';
import { CommonUtilService } from '../../_helpers/CommonUtilService';
import { thDatatables } from '../../_helpers/thDatatables';
import { MasterDataService } from '../../_services/MasterData.service';
declare var pagingInputInit: any;

@Component({
  selector: 'app-marine-support-management',
  templateUrl: './marine-support-management.component.html',
  styleUrls: ['./marine-support-management.component.css']
})
export class MarineSupportManagementComponent implements OnInit {
  masterObj: any;
    blockList: any;

  constructor(private MasterData: MasterDataService,
    public _matDialog: MatDialog,
    private cdr: ChangeDetectorRef,
    private router: Router,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,) { }

  @Input() formZone: FormGroup;
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any;
  dialogRef: any;
  activeList: any[] = [{
    Code: "Y",
    Description: "Active"
  }, {
    Code: "N",
    Description: "Inactive"
    }];
  parameterList: any[] = [];
  activityList: any[] = [];
  projectList: any[] = [];
  public dataa = [
    { seq: '1', name: 'M9', type: 'Production', project: 'ZPQ', activity: 'Production', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
    { seq: '2', name: 'M9', type: 'Drilling (Development)', project: 'Rig', activity: 'M9 Phase 1 D', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
    { seq: '3', name: 'M9', type: 'Drilling (Development)', project: 'Rig', activity: 'M9 Phase 1 D - INFILL', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
    { seq: '4', name: 'M9', type: 'Drilling (Development)', project: 'Rig', activity: 'M9 Phase 1 E', code: false, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
    { seq: '5', name: 'M9', type: 'Drilling (Appraisal)', project: 'Rig', activity: 'M9 East Appraisal', code: true, by: 'System Admin', date: '2021-04-22 13:39:39.117' },
  ];

  ngOnInit(): void {

    this.route.data.subscribe(data =>
      this.masterObj = data.masterObj
    );

    this.blockList = this.masterObj.tcBlock;
    this.parameterList = this.masterObj.tcParameter;
    this.activityList = this.masterObj.tcActivity;
    this.projectList = this.masterObj.tcProject;

    this.formZone = this.formBuilder.group({
      'BlockCode': new FormControl(null),
      'ParameterCode': new FormControl(null),
      'ProjectCode': new FormControl(null),
      'ActivityCode': new FormControl(null),
      'ActiveFlag': new FormControl(null)
    });

    pagingInputInit();
    this.dtOptions = {
      pagingType: 'input',
      dom: 'B<"float-right"l>tip',
      language: thDatatables,
      processing: true,
      serverSide: true,
      responsive: true,
      scrollX: true,
      autoWidth: true,
      pageLength: 10,
      lengthMenu: [10, 25, 50, 100],
      //Configure the buttons
      buttons: [
        {
          text: '+ New',
          className: 'mat-focus-indicator btn-green mat-raised-button mat-button-base _mat-animation-noopable button-margin-bottom',
          action: () => {
            this.isAdd();
          },
          init: function (api, node, config) {
            $(node).removeClass('dt-button')
          }
        }
      ],
      "columns": [
        {
          data: null,
          className: 'dt-head-center dt-body-center',
          width: '10px',
          render: this.renderAction
        },
        {
          data: 'seqnum',
          className: 'dt-head-center dt-body-center',
          "width": "10px"
        },
        {
          data: 'Block_Name',
          sortProperty: 'r.Block_Code',
          className: 'dt-head-center dt-body-center',
          "width": "50px"
        },
        {
          data: 'Parameter_Name',
          sortProperty: 'r.Parameter_Code',
          className: 'dt-head-center dt-body-center',
          "width": "125px"
        },
        {
          data: 'Project_Name',
          sortProperty: 'r.Project_Code',
          className: 'dt-head-center dt-body-center',
          "width": "125px"
        },
        {
          data: 'Activity_Name',
          sortProperty: 'r.Activity_Code',
          className: 'dt-head-center dt-body-center',
          "width": "125px"
        },
        {
          data: 'Active_flag',
          //sortProperty: 'countReservoir',
          className: 'dt-head-center dt-body-center',
          width: '50px',
          render: this.activeFlagData
        },
        {
          data: 'Update_Dttm',
          //sortProperty: 'ApproveName',
          className: 'dt-head-center dt-body-center',
          width: '100px',
          render: CommonUtilService.formatDateTime
        },
        {
          data: 'Update_Name',
          //sortProperty: 'ApproveDttm',
          className: 'dt-head-center dt-body-center',
          width: '100px'
        }
      ],
      columnDefs: [
        {
          "targets": [0, 1],
          "orderable": false
        }
      ],
      order: [[2, 'asc']],
      ajax: (dataTablesParameters: any, callback) => {
        var criteria = {
          'start': dataTablesParameters.start,
          'length': dataTablesParameters.length,
          "sorterProperty": (this.dtOptions.columns[dataTablesParameters.order[0].column].sortProperty) ?
            this.dtOptions.columns[dataTablesParameters.order[0].column].sortProperty :
            this.dtOptions.columns[dataTablesParameters.order[0].column].data,
          "direction": dataTablesParameters.order[0].dir,
          'BlockCode': this.formZone.get("BlockCode").value,
          'ParameterCode': this.formZone.get("ParameterCode").value,
          'ProjectCode': this.formZone.get("ProjectCode").value,
          'ActivityCode': this.formZone.get("ActivityCode").value,
          'ActiveFlag': (this.formZone.get("ActiveFlag").value == "Y") ? true : (this.formZone.get("ActiveFlag").value == "N") ? false : null,
        }

        this.MasterData.searchMarineSupportPaging(criteria).subscribe(res => {
          //console.log(res);
          callback({
            recordsTotal: res.iTotalRecords,
            iTotalDisplayRecords: res.iTotalDisplayRecords,
            recordsFiltered: res.recordsFiltered,
            data: res.detail
          });
        });
      },
      createdRow: (row: any, data: any | Object, index: number) => {
        const self = this;

        row.querySelectorAll('.mat-edit').forEach(function (item) {
          item.addEventListener('click', function ($event) {
            self.router.navigate(['//marine-support-management-modal', data.IdEncoded, "edit"]);
          });
        });
      }
    }
  }

  renderAction(data, type, full, meta) {
    //console.log(data);
    return '<mat-icon role="img"  matripple="" class="gray-icon mat-icon notranslate mat-ripple material-icons mat-edit" style="cursor: pointer;" aria-hidden="true" ng-reflect-color="primary" ng-reflect-radius="15" title="Edit">edit</mat-icon>'
  }

  activeFlagData(data) {
    if (data == true) {
      return "Active";
    }
    return "Inactive";
  }

  isAdd() {
    this.router.navigate(['/marine-support-management-modal', 0, "add"]);
  }

  isSearch() {
    //this.isFirst = false;
    this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
      dtInstance.ajax.reload();
    });
  }

  isClear() {
    this.formZone.get("BlockCode").setValue(null);
    this.formZone.get("ParameterCode").setValue(null);
    this.formZone.get("ProjectCode").setValue(null);
    this.formZone.get("ActivityCode").setValue(null);
    this.formZone.get("ActiveFlag").setValue(null);

    this.isSearch();
  }
}
