import { Component, Inject, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ErrorMessageService } from '../../../_services/ErrorMessageService';

@Component({
  selector: 'app-project-management-modal',
  templateUrl: './project-management-modal.component.html',
  styleUrls: ['./project-management-modal.component.css']
})
export class ProjectManagementModalComponent implements OnInit {

  @Input() formZone: FormGroup;

  constructor(public matDialogRef: MatDialogRef<any>,
        @Inject(MAT_DIALOG_DATA) private _data: any,
    private formBuilder: FormBuilder,
    private confirmDialog: MatDialog,
    private alertDialog: MatDialog,
    public _matDialog: MatDialog ) { }

  ngOnInit(): void {
    console.log(this._data);
    if (this._data && this._data?.editObj?.ProjectCode) {
      this.formZone = this.formBuilder.group({
        'ProjectCode': new FormControl({ value: this._data?.editObj?.project_Code, disabled: false }, Validators.compose([Validators.required])),
        'ActiveFlag': new FormControl({ value: this._data?.editObj?.active_Flag, disabled: false }, Validators.compose([Validators.required])),
        'ProjectName': new FormControl({ value: this._data?.editObj?.project_Name, disabled: false }, Validators.compose([Validators.required])),
        'ProjectDescrition': new FormControl({ value: this._data?.editObj?.project_Description, disabled: false }, Validators.compose([Validators.required])),
      });
    } else {
      this.formZone = this.formBuilder.group({
        'ProjectCode': new FormControl({ value: this._data?.editObj?.project_Code, disabled: false }, Validators.compose([Validators.required])),
        'ActiveFlag': new FormControl(true),
        'ProjectName': new FormControl({ value: this._data?.editObj?.project_Name, disabled: false }, Validators.compose([Validators.required])),
        'ProjectDescrition': new FormControl({ value: this._data?.editObj?.project_Description, disabled: false }, Validators.compose([Validators.required])),
      });
    }

  }

  getErrorMessage(obj) {
    return ErrorMessageService.getErrorMessageByCode(obj);
  }

  save() {

  }

}
