  initailCampaignTableByMode() {
    this.campaignService.getCampaignTableByMode(this.granttType).subscribe(
      result => {

       // console.log(result)
        
        if (this.granttType == 'M') {
          this.year = result.thisYear;
          result.campaignTableList.forEach(function (data) {
            for (var i = 1; i < 13; ++i) {
              var index = data["MonthYearList"].findIndex(x => x.month == i && x.year == result.thisYear);
        
              data['C' + i] = index==-1?0:1;
             
            }
          });
          this.dataList = result.campaignTableList;
          this.dataListOrg = $.extend(true, [], this.dataList);
        }
        //this.toggleBtn('C');
       // alert('sad')

        var then = this
        //setInterval(function () {
        setTimeout(function () {
          try {
            $('span[level="2"]').unbind("dragstart");

            $(document).unbind('dragenter drop');
            $('span[id^="C"]').unbind("dragend");
          } catch { }
          then.setDrop();

          then.toggleBtn('C')

        }, 500);
      }
      , error => console.error(error));
  }

