import { Component, OnInit, Inject, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { CampaignService } from '../../../_services/Campaign.service';
import { CommonUtilService } from '../../../_helpers/CommonUtilService';
import { CalculateCostService } from '../../../_helpers/CalculateCostService';
import { ConfirmModalComponent } from '../../../common-modal/confirm-modal/confirm-modal.component';
import { AlertModalComponent } from '../../../common-modal/alert-modal/alert-modal.component';
@Component({
  selector: 'app-campaign-marine-support-modal',
  templateUrl: './campaign-marine-support-modal.component.html',
  styleUrls: ['./campaign-marine-support-modal.component.css']
})
export class CampaignMarineSupportModalComponent implements OnInit {
  @Input() formZone: FormGroup;
  campaignMarineSupportMonth: any;

  countingList = 0;

  constructor(public matDialogRef: MatDialogRef<any>,
    @Inject(MAT_DIALOG_DATA) private _data: any,
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
   
    private confirmDialog: MatDialog,
    private alertDialog: MatDialog,
    private campaignService: CampaignService) { }

  ngOnInit(): void {
    this.campaignMarineSupportMonth = this._data.resolverData
    this.formZone = new FormGroup({
      rigName: new FormControl({ value: this._data.ParameterName, disabled: true }),
      campaignName: new FormControl({ value: this._data.CampaignName, disabled: true }),
      month: new FormControl({ value: CommonUtilService.getMonthShortNameByCode(this.campaignMarineSupportMonth.MonthCode), disabled: true }),
      year: new FormControl({ value: this.campaignMarineSupportMonth.Year, disabled: true })
      
    });

    this.loadCost();
  }
  formatDateTime(UpdateDttm) {
    return CommonUtilService.formatDateTime(UpdateDttm)
  }

  loadCost() {

    for (var i = 0; i < this.campaignMarineSupportMonth.campaignMarineSupportMonthCostList.length; i++) {
      var costObj = this.campaignMarineSupportMonth.campaignMarineSupportMonthCostList[i];
      this.formZone.addControl("fromValueCf_" + i, new FormControl({ value: costObj.ValueCf, disabled: (costObj.VariableTypeCode == 'D') }, Validators.compose([Validators.required])));
      this.formZone.updateValueAndValidity();
      costObj["fromValueCf"] = "fromValueCf_" + i;


      this.countingList++;

    }
  }

  calculateCost() {
    const self = this;
    this.campaignMarineSupportMonth.campaignMarineSupportMonthCostList.forEach(function (data) {
      data["ValueCf"] = parseFloat(self.formZone.get(data["fromValueCf"]).value);

    });

    const calObj = new CalculateCostService();
   // calObj._days = parseFloat(self.formZone.get("dayCount").value);
    calObj.calculateCostList(this.campaignMarineSupportMonth.campaignMarineSupportMonthCostList, this.campaignMarineSupportMonth.costParentDtoList);
    this.campaignMarineSupportMonth.campaignMarineSupportMonthCostList.forEach(function (data) {

      self.formZone.get(data["fromValueCf"]).setValue(data["ValueCf"]);
    });
  }


  save() {

    const self = this;
  

    this.campaignMarineSupportMonth.campaignMarineSupportMonthCostList.forEach(function (data) {
      data["ValueCf"] = parseFloat(self.formZone.get(data["fromValueCf"]).value);
    });



    const dialogRef = this.confirmDialog.open(ConfirmModalComponent, {
      disableClose: true,
      data: {
        message: 'Would you like to save?',
        buttonText: {
          ok: 'Save',
          cancel: 'No'
        }
      }
    });

    dialogRef.afterClosed().subscribe((confirmed: boolean) => {
      if (confirmed) {

        this.campaignService.updateCampaignMarineSupportMonth(this.campaignMarineSupportMonth).subscribe(
          result => {
            const alertDialog = this.alertDialog.open(AlertModalComponent, {
              disableClose: true,
              data: {
                message: 'Save Successfully.',
                buttonText: {
                  cancel: 'OK'
                }
              }
            });
            alertDialog.afterClosed()
              .subscribe(response => {
                this.matDialogRef.close('complete')
              });
          },
          error => console.error(error)
        )
      }
    });

  }

}
