  initialChart1(data) {
    //var nav = this.navMenuComponent;
    //Highcharts.chart({});
    Highcharts.chart({


      chart: {
        renderTo: 'containerChart1',
        type: 'gauge',
        plotBackgroundColor: null,
        plotBackgroundImage: null,
        plotBorderWidth: 0,
        plotShadow: false,
      },

      title: {
        text: data.toFixed(2) + '% of ' + this.currentYear+' AB (YTD)'
      },
      credits: {
        enabled: false
      },
      exporting: { enabled: false },
      pane: {
        startAngle: -120,
        endAngle: 120,
        background: [{
          backgroundColor: {
            linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
            stops: [
              [0, '#FFF'],
              [1, '#333']
            ]
          },
          borderWidth: 0,
          outerRadius: '109%'
        }, {
          backgroundColor: {
            linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
            stops: [
              [0, '#333'],
              [1, '#FFF']
            ]
          },
          borderWidth: 1,
          outerRadius: '107%'
        }, {
          // default background
        }, {
          backgroundColor: '#DDD',
          borderWidth: 0,
          outerRadius: '105%',
          innerRadius: '103%'
        }]
      },

      // the value axis
      yAxis: {
        min: 0,
        max: 100,

        minorTickInterval: 'auto',
        minorTickWidth: 1,
        minorTickLength: 10,
        minorTickPosition: 'inside',
        minorTickColor: '#666',

        tickPixelInterval: 30,
        tickWidth: 2,
        tickPosition: 'inside',
        tickLength: 10,
        tickColor: '#666',
        labels: {
          step: 2,
          //rotation: 'auto'
        },
        /*title: {
          text: 'km/h'
        },*/
        plotBands: [{
          from: 0,
          to: 60,
          //color: '#55BF3B' // green
          color: '#DF5353' // red
        }, {
          from: 60,
          to: 80,
          //color: '#DDDF0D' // yellow
          color: '#DF5353' // red
        }, {
          from: 80,
          to: 100,
          //color: '#DF5353' // red
          color: '#DF5353' // red
        }]
      },

      series: [{
        name: '% of ' + this.currentYear + ' AB (YTD)',
        type: undefined,
        data: [data],
        dataLabels: {
          format: '{point.y:.2f}'
        },
        tooltip: {
          //valueSuffix: ' %',
          pointFormat: '{point.y:.2f}%'
        }
      }]

    });

  }

